.PromoCarousel {
  box-sizing: content-box;
  height: 210px;
  border-top: 1px solid #dfdfe1;
  border-bottom: 1px solid #dfdfe1;
  overflow: hidden;
}
.PromoCarousel li {
  padding: 0;
}
.PromoCarousel div {
  padding: 0;
}
.PromoCarousel .slide {
  height: 210px;
  user-select: none;
  cursor: pointer;
  object-fit: cover;
  object-position: center center;
  aspect-ratio: 2/1;
}

