$count-slides: 4;

.PromoCarousel {
  box-sizing: content-box;
  height: 210px;
  border-top: 1px solid #dfdfe1;
  border-bottom: 1px solid #dfdfe1;
  overflow: hidden;

  li {
    padding: 0;
  }

  div {
    padding: 0;
  }

  .slide {
    height: 210px;
    user-select: none;
    cursor: pointer;
    object-fit: cover;
    object-position: center center;
    aspect-ratio: 2 / 1;

    // TODO: TEMP BIGGER NUMBER
    //font-size: 6rem;
    //color: white;
    //text-align: center;
    // TODO: TEMP BIGGER NUMBER
  }

